import {Button, Col, Form, Input, Row, Select} from "antd";
import React, {useEffect} from "react";
import {Status} from "../status";
import {Rule} from "antd/lib/form";
import {IStatus} from "../defaultData";

const {Option} = Select;
const {TextArea} = Input;

interface IOwnProps {
    initialValues: any;
    handleSubmit: any;
    statuses: IStatus[];
    isCreateClient: boolean;
}

const checkPhones = (rule: Rule, phones: string | undefined) => {
    if (!phones) {
        return Promise.reject('Обязательное поле')
    }
    const phonesArr = phones.split(' ').map((phone) => phone.trim()).filter((value) => !!value);
    const error = phonesArr.some((phone) => phone.length !== 13 || phone[0] !== '+');
    return error
        ? Promise.reject('Номер должен соответствовать формату +375112223331')
        : Promise.resolve();
};

const checkEmails = (rule: Rule, emails: string | undefined) => {
    if (!emails) {
        return Promise.reject('Обязательное поле')
    }
    const phonesArr = emails.split(' ').map((phone) => phone.trim()).filter((value) => !!value);
    const error = phonesArr.some((phone) => !(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(phone)));
    return error
        ? Promise.reject('Неверный формат почтового адреса')
        : Promise.resolve();
};

export const FormWrapper: React.FC<IOwnProps> = (props) => {
    const {statuses, handleSubmit, initialValues, isCreateClient} = props
    const [form] = Form.useForm();

    const statusOptionEls = statuses.map((status) => <Option key={status.id} value={status.id}><Status status={status}/></Option>);

    useEffect(() => {form.setFieldsValue(initialValues)}, [initialValues])
    return (
        <Form
            form={form}
            name={"clientForm"}
            onFinish={handleSubmit}
        >
            <Row gutter={16}>
                <Col span={12}>
                    <Form.Item
                        name={'status'}
                        label={'Статус сделки'}
                        rules={[{required: true, message: 'Обязательное поле'}]}
                        labelCol={{span: 24}}
                    >
                        <Select
                            placeholder={'Выберите статус сделки'}
                        >
                            {statusOptionEls}
                        </Select>
                    </Form.Item>
                </Col>
                <Col span={12}>
                    <Form.Item
                        name={'organization'}
                        label={'Организация'}
                        rules={[{required: true, message: 'Обязательное поле'}]}
                        labelCol={{span: 24}}
                    >
                        <Input/>
                    </Form.Item>
                </Col>
            </Row>
            <Row gutter={16}>
                <Col span={12}>
                    <Form.Item
                        name={'additionalInf'}
                        label={'Доп. информация'}
                        labelCol={{span: 24}}
                        initialValue={''}
                    >
                        <TextArea rows={1}/>
                    </Form.Item>
                </Col>
                <Col span={12}>
                    <Form.Item
                        name={'contactPerson'}
                        label={'Контактное лицо'}
                        rules={[{required: true, message: 'Обязательное поле'}]}
                        labelCol={{span: 24}}
                    >
                        <Input/>
                    </Form.Item>
                </Col>
            </Row>
            <Row gutter={16}>
                <Col span={12}>
                    <Form.Item
                        name={'phoneNumbers'}
                        label={'Контактные номера'}
                        labelCol={{span: 24}}
                        rules={[{validator: checkPhones}]}
                    >
                        <TextArea rows={1}/>
                    </Form.Item>
                </Col>
                <Col span={12}>
                    <Form.Item
                        name={'emails'}
                        label={'Email - адреса'}
                        rules={[{validator: checkEmails}]}
                        labelCol={{span: 24}}
                    >
                        <TextArea rows={1}/>
                    </Form.Item>
                </Col>
            </Row>
            <Col span={24}>
                <Form.Item
                    name={'region'}
                    label={'Регион'}
                    labelCol={{span: 24}}
                    rules={[{required: true, message: 'Обязательное поле'}]}
                >
                    <Input/>
                </Form.Item>
            </Col>
            <div className={"clientModalButtonsRow"}>
                <Button htmlType="button" onClick={() => form.resetFields()}>
                    Очистить
                </Button>
                <Button type="primary" htmlType="submit">
                    {isCreateClient ? 'Создать' : 'Сохранить'}
                </Button>
            </div>
        </Form>
    )
};
