import React from 'react';
import {ITableRowRecord} from "../App";
import './salesFunnel.css';
import {IStatus} from "../defaultData";

interface IOwnProps {
    extendedClients: ITableRowRecord[];
    statuses: IStatus[];
}

export const SalesFunnel: React.FC<IOwnProps> = (props) => {
    const {extendedClients, statuses} = props;
    const maxValue = extendedClients.length;
    const sortedStatuses = statuses.sort((a, b) => a.priority - b.priority);
    const rowEls = sortedStatuses.map((status, index) => {
        const {bodyColor, name} = status;
        let currentCount = 0;
        for (let i = index; i < sortedStatuses.length; i++) {
            currentCount += extendedClients.filter((client) => client.offerStatusId === sortedStatuses[i].id).length;
        }
        const percent = currentCount / maxValue * 100;
        return (
            <div className="salesFunnelRow" style={{width: percent + '%', backgroundColor: bodyColor}}>
                <span>{`${name} (${Math.floor(percent)}%)`}</span>
            </div>
        )
    });
    return (
        <div className="salesFunnel">
            {rowEls}
        </div>
    )
};
