import React, {useState} from 'react';
import './App.css';
import {Table, Card, Button, Dropdown, Menu} from "antd";
import {
    getColumns,
    IClient,
    IManager,
    IStatus,
    preloadedClients,
    preloadedManagers,
    preloadedStatuses
} from "./defaultData";
import {ModalWrapper} from "./addAndEditClientModal";
import Manager from './assets/manager.png';
import {CommentsModal} from "./commentsModal";
import {SalesFunnel} from "./salesFunnel";
import {WorkerStatistics} from "./workerStatistics";

export interface ITableRowRecord extends IClient{
    foundStatus: IStatus | undefined;
    foundManager: IManager | undefined;
}

function App() {
    const [clients, setClients] = useState<IClient[]>(preloadedClients);
    const [managers, setManagers] = useState<IManager[]>(preloadedManagers);
    const [currentManager, setCurrentManager] = useState<IManager>(preloadedManagers[0]);
    const [statuses, setStatuses] = useState<IStatus[]>(preloadedStatuses);
    const [clientModalVisible, setClientModalVisible] = useState(false);
    const [commentsModalVisible, setCommentsModalVisible] = useState(false);
    const [selectedClientId, setSelectedClientId] = useState<number | undefined>();
    const [isCreateClient, setIsCreateClient] = useState(true);
    const extendedClients = clients.map((client) => {
        const foundManager = managers.find((manager) => manager.id === client.managerId);
        const foundStatus = statuses.find((status) => status.id === client.offerStatusId);
        return {...client, foundManager, foundStatus};
    });
    const editableClientData = clients.find((client) => client.id === selectedClientId);
    const extendedClientData = extendedClients.find((extendedClient) => extendedClient.id === selectedClientId)
    const calcRowClassName = (tableRowRecord: ITableRowRecord) => {
        return (tableRowRecord.id === selectedClientId)
            ? 'tableRowSelected'
            : 'tableRow';

    }

    const onRowHandlers = (tableRowRecord: ITableRowRecord) => ({
        onClick: () => {
            if (tableRowRecord.id === selectedClientId) {
                return setSelectedClientId(undefined)
            }else {
                setSelectedClientId(tableRowRecord.id);
            }

        }
    });

    const managerEls = managers
        .filter((manager) => manager.id !== currentManager.id)
        .map((manager) => (
            <Menu.Item>
                <div
                    onClick={() => setCurrentManager(manager)}
                    className={"currentManager dropdownManager"}
                >
                    <img src={Manager} alt="manager"/>
                    <span>{manager.name}{manager.administrator && <span className={"admin"}>(Администратор)</span>}</span>
                </div>
            </Menu.Item>
        ));
    const dropdownEl = <Menu>{managerEls}</Menu>;

    return (
        <div className="app">
            <header className="header">
                <Dropdown overlay={dropdownEl} placement="bottomCenter" trigger={['click']}>
                    <div className={"currentManager"}>
                        <img src={Manager} alt="manager"/>
                        <span>{currentManager.name}</span>
                    </div>
                </Dropdown>
                <div className={"headerText"}>
                <span className={"crmDescription"}>
                    Улучшаем продажи с
                </span>
                    <span className={"crmName"}>DP CRM</span>
                </div>
            </header>
            <div className={"tableDescription"}>
                <span>Текущие и потенциальные партнеры компании</span>
                <div className={"tableActions"}>
                <Button
                    onClick={() => {
                        setClientModalVisible(true);
                        setIsCreateClient(true);
                    }}
                    type="primary"
                >
                    Добавить партнера
                </Button>
                <Button
                    onClick={() => {
                        setClientModalVisible(true);
                        setIsCreateClient(false);
                    }}
                    disabled={!selectedClientId}
                >
                    Внести изменения
                </Button>
                </div>
            </div>
            <Table
                className={"table"}
                columns={getColumns(setCommentsModalVisible, !!selectedClientId)}
                dataSource={extendedClients}
                bordered={true}
                rowClassName={calcRowClassName}
                rowKey={(record: ITableRowRecord) => record.id.toString()}
                onRow={onRowHandlers}
                scroll={{ y: 450, x: true }}
            >
            </Table>

          { currentManager.administrator && <div className="statRow">
                <div>
                    <span className={"salesFunnelLabel"}>Воронка продаж</span>
                    <SalesFunnel
                        extendedClients={extendedClients}
                        statuses={statuses}
                    />
                </div>
                <div>
                    <span className={"salesFunnelLabel"}>Статистика сотрудников</span>
                    <WorkerStatistics
                        extendedClients={extendedClients}
                        managers={managers}
                    />
                </div>
            </div>}

            <ModalWrapper
                clientModalVisible={clientModalVisible}
                editableClient={editableClientData}
                setClientModalVisible={setClientModalVisible}
                currentManager={currentManager}
                statuses={statuses}
                isCreateClient={isCreateClient}
                setClients={setClients}
                managers={managers}
            />
            {extendedClientData && <CommentsModal
                commentsModalVisible={commentsModalVisible}
                setCommentsModalVisible={setCommentsModalVisible}
                clientTableRecord={extendedClientData}
                managers={managers}
                setClients={setClients}
                currentManager={currentManager}
            />}
        </div>
    );
}

export default App;
