import React, {Dispatch, SetStateAction, useEffect, useState} from "react";
import {Col, Modal, Row} from "antd";
import {IClient, IComment, IManager, IStatus} from "../defaultData";
import './clientModal.css';
import {Form, Input, Button, Select} from 'antd';
import Manager from "../assets/manager.png";
import {Status} from "../status";
import {Store} from "antd/lib/form/interface";
import {Rule} from "antd/lib/form";
import {FormWrapper} from "./FormWrapper";

const {Option} = Select;
const {TextArea} = Input;

interface IOwnProps {
    editableClient?: IClient;
    clientModalVisible: boolean;
    setClientModalVisible: (visible: boolean) => void;
    currentManager: IManager;
    statuses: IStatus[];
    isCreateClient: boolean;
    setClients: Dispatch<SetStateAction<IClient[]>>;
    managers: IManager[];
}



export const ModalWrapper: React.FC<IOwnProps> = (props) => {
    const {managers, editableClient, isCreateClient, setClientModalVisible, clientModalVisible, currentManager, statuses, setClients} = props;

    const foundManager = managers.find((manager) => manager.id === editableClient?.managerId);

    const getInitialValues = () => {
        if (!isCreateClient && editableClient) {
            return {
                status: editableClient.offerStatusId,
                organization: editableClient.organizationName,
                additionalInf: editableClient.additionalInfo,
                contactPerson: editableClient.contactPerson,
                phoneNumbers: editableClient.phoneNumbers.join(' '),
                emails: editableClient.emails.join(' '),
                region: editableClient.region,
            }
        } else {
            return {
                status: null,
                organization: '',
                additionalInf: '',
                contactPerson: '',
                phoneNumbers: '',
                emails: '',
                region: '',
            }
        }
    }

    const handleSubmit = (values: Store) => {
        const newClient = {
            id: isCreateClient ? Math.random() : editableClient!.id,
            managerId: isCreateClient ? currentManager.id : editableClient!.managerId,
            offerStatusId: values.status,
            comments: isCreateClient ? [] : editableClient!.comments,
            organizationName: values.organization,
            additionalInfo: values.additionalInf,
            contactPerson: values.contactPerson,
            phoneNumbers: values.phoneNumbers.split(' ').map((phone: string) => phone.trim()).filter((value: string) => !!value),
            emails: values.emails.split(' ').map((email: string) => email.trim()).filter((value: string) => !!value),
            region: values.region,
        };
        setClientModalVisible(false);
        if (isCreateClient) {
            setClients((prevClients) => {
                Modal.success({title: 'Партнер добавлен!'});
                return [...prevClients, newClient]
            })
        } else {
            setClients((prevClients) => {
                const withoutEditable = prevClients.filter((client) => client.id !== editableClient?.id);
                return [...withoutEditable, newClient]
            })
        }

    };

    return (
        <Modal
            className={"clientModal"}
            title={isCreateClient ? 'Добавление партнера ' : 'Редактирование партнера'}
            centered={true}
            cancelText={"Отменить"}
            okText={isCreateClient ? "Создать" : "Изменить"}
            visible={clientModalVisible}
            onCancel={() => setClientModalVisible(false)}
            footer={null}
            destroyOnClose={true}
        >
            <div className="clientModalBody">
                <div className={"currentManager managerInModal"}>
                    <img src={Manager} alt="manager"/>
                    <span>{`${isCreateClient ? currentManager.name : foundManager?.name}(Менеджер клиента)`}</span>
                </div>
               <FormWrapper
                   initialValues={getInitialValues()}
                   statuses={statuses}
                   handleSubmit={handleSubmit}
                   isCreateClient={isCreateClient}
               />
            </div>
        </Modal>
    )
};
