import React from 'react';
import {ITableRowRecord} from "../App";
import './workerStatistics.css';
import {IManager} from "../defaultData";

interface IOwnProps {
    extendedClients: ITableRowRecord[];
    managers: IManager[];
}

export const WorkerStatistics: React.FC<IOwnProps> = (props) => {
    const {extendedClients, managers} = props;

    const rowEls = managers.map((manager) => {
        const clientCount = extendedClients.filter((client) => client.managerId === manager.id).length;
        const outGoing = Math.floor(Math.random() * 1000);
        const missed = Math.floor(Math.random() * 300);

        return (
            <>
                <span className="managerInfoStatistics">
                   {manager.name}
                </span>
                <div className="workerRow">

                    <div className="allClients">
                        {`Всего создано партнеров: `}
                        <span className={"clientCount"}>
                        {clientCount}
                    </span>
                    </div>

                    <div className="callsStatistics">
                        <div>{`Исходящих звонков:   `}<span className={"outGoing"}>{outGoing}</span></div>
                        <div>{`Пропущенных звонков: `}<span className={"missed"}>{missed}</span></div>
                    </div>
                </div>
            </>
        )
    });
    return (
        <div className="workerStatistics">
            {rowEls}
        </div>
    )
};
