import React, {ChangeEvent, Dispatch, SetStateAction, useEffect, useRef, useState} from "react";
import {Col, Empty, Modal, Row, Tooltip} from "antd";
import './commentsModal.css';
import {Form, Input, Button, Select} from 'antd';
import {ITableRowRecord} from "../App";
import {dateFormat, IClient, IComment, IManager} from "../defaultData";
import moment from "moment";
import 'moment/locale/ru'

const {TextArea} = Input;

interface IOwnProps {
    clientTableRecord: ITableRowRecord;
    commentsModalVisible: boolean;
    setCommentsModalVisible: Dispatch<SetStateAction<boolean>>;
    managers: IManager[];
    setClients: Dispatch<SetStateAction<IClient[]>>
    currentManager: IManager;
}

moment.locale('rus');

export const CommentsModal: React.FC<IOwnProps> = (props) => {
    const {
        clientTableRecord: {
            foundManager,
            organizationName,
            comments,
            contactPerson,
            id,
        },
        commentsModalVisible,
        setCommentsModalVisible,
        managers,
        setClients,
        currentManager,
    } = props;

    const [newCommentValue, setNewCommentValue] = useState('');

    const handleCommentTextChange = (e: ChangeEvent<HTMLTextAreaElement>) => {
        setNewCommentValue(e.currentTarget.value)
    };

    const handleSubmitComment = () => {
        setClients((clients) => {
            const currentClientIndex = clients.findIndex((client) => client.id === id);
            if (currentClientIndex === -1) {
                return clients;
            }
            const newCommentRecord: IComment = {
                comment: newCommentValue.trim(),
                managerId: currentManager.id,
                createdAt: moment().format(dateFormat),
            };
            clients[currentClientIndex].comments = [...clients[currentClientIndex].comments, newCommentRecord];
            setNewCommentValue('');
            return [...clients]
        })
    };

    const commentsEl = comments.map((comment) => {
        const commentCreator = managers.find((manager) => manager.id === comment.managerId);
        const timeFrom = moment(comment.createdAt, dateFormat).from(moment());
        return (
            <div
                className={'comment'}
                key={comment.comment}
            >
                <div className="commentDescription">
                    {
                        (comment.managerId === foundManager?.id)
                            ? <Tooltip placement="topLeft" title={'Текущий менеджер клиента'}>
                                <span
                                    className={'activeComment'}
                                >
                                    {commentCreator && commentCreator.name || 'Неизвестный автор'}
                                </span>
                            </Tooltip>
                            : <span>
                                    {commentCreator && commentCreator.name || 'Неизвестный автор'}
                                </span>
                    }
                    <span>{timeFrom}</span>
                </div>
                <div className="commentTextBox">
                    {comment.comment}
                </div>
            </div>
        )
    });
    return (
        <Modal
            className={"commentsModal"}
            title={"Комментарии к клиенту"}
            centered={true}
            visible={commentsModalVisible}
            onCancel={() => setCommentsModalVisible(false)}
            destroyOnClose={true}
            footer={null}
        >
            <div className={"commentsModalBody"}>
                <div className="clientDescriptionInComment">
                    <div> Партнер <span>{organizationName}</span></div>
                    <div>Контактное лицо <span>{contactPerson}</span></div>
                    <div>{`Менеджер клиента `}
                        <span>{foundManager?.name || 'Менеджер не определен'}</span>
                    </div>
                </div>
                <div className="comments">
                    {commentsEl.length ? commentsEl.reverse() : <Empty description={'Оставьте первый комментарий'} image={Empty.PRESENTED_IMAGE_SIMPLE} />}
                </div>
                <div className={"addCommentBox"}>
                    <TextArea rows={3} value={newCommentValue} onChange={handleCommentTextChange}/>
                    <Button onClick={handleSubmitComment} type="primary"
                            disabled={!newCommentValue.trim()}>Отправить</Button>
                </div>
            </div>

        </Modal>
    )
};
