import React from "react";
import {IStatus} from "../defaultData";
import './status.css'

interface IOwnProps {
    status: IStatus;
}

export const Status: React.FC<IOwnProps> = (props) => {
    const {name, bodyColor, textColor} = props.status;
    return (
        <div className={"status"} style={{backgroundColor: bodyColor, color: textColor}}>
            {name}
        </div>
    )
};
