export const getColumns  = (setCommentsModalVisible: Dispatch<SetStateAction<boolean>>, isSelected: boolean) => [
    {
        title: 'Менеджер клиента',
        dataIndex: 'foundManager',
        key: 'foundManager',
        render: (data: IManager) => data.name
    },
    {
        title: 'Статус сделки',
        dataIndex: 'foundStatus',
        key: 'foundStatus',
        render: (data: IStatus) => <Status status={data}/>

    },
    {
        title: 'Комментарии',
        dataIndex: 'comments',
        key: 'comments',
        render: (data: IComment[]) => <div style={{display:'flex', flexWrap: 'nowrap'}}>{data.length} <img onClick={(e) =>
        {
            e.stopPropagation();
            isSelected && setCommentsModalVisible(true)
        }} className={"commentImg"} src={Comment} alt="comment"/> </div>,
    },
    {
        title: 'Название организации',
        dataIndex: 'organizationName',
        key: 'organizationName',
    },
    {
        title: 'Доп. информация',
        dataIndex: 'additionalInfo',
        key: 'additionalInfo',
    },
    {
        title: 'Контактное лицо',
        dataIndex: 'contactPerson',
        key: 'contactPerson',
    },
    {
        title: 'Контактные номера',
        dataIndex: 'phoneNumbers',
        key: 'phoneNumbers',
        render: (data: string[]) => data.map((phone) => <><a href={`tel:${phone}`}>{phone}</a><br/></>),
    },
    {
        title: 'Email - адреса',
        dataIndex: 'emails',
        key: 'emails',
        render: (data: string[]) => data.map((email) => <><a href={`mailto:${email}`}>{email}</a><br/></>),
    },
    {
        title: 'Регион',
        dataIndex: 'region',
        key: 'region',
    }
];

